import React from 'react';
import { withRouter } from 'react-router-dom';
import './Burger.css';
import BurgerIngredients from './BurgerIngredients/BurgerIngredients'


const Burger = (props) => {
  let transformedIngredients = Object.keys(props.ingredients).map((igKey) => {
    return [...Array(props.ingredients[igKey])].map((_, i) => { return <BurgerIngredients key={igKey + i} type={igKey} />;
    }
  )}).reduce((arr, el) => {
        return arr.concat(el)
      }, []);

  if (transformedIngredients.length === 0) {
    transformedIngredients = <h2>Your cravings here</h2>
  };
  return(
    <div className="Burger" id={window.location.href.includes('checkout') ? "transparent" : null}>
      <BurgerIngredients type="bread-top"/>
      {transformedIngredients}
      <BurgerIngredients type="bread-bottom"/>

    </div>
  );
}

export default withRouter(Burger);



